<template>
  <div class="pa-6">
    <v-data-table
      :loading="loading"
      :headers="headers"
      :items="encashments.data"
      :options.sync="pagination"
      :footer-props="{
        itemsPerPageOptions: itemsPerPageOptions
      }"
      :server-items-length="encashments.meta ? encashments.meta.total : 0"
      class="elevation-1 overflow-hidden" dark
    >
      <template v-slot:top>
        <v-container
          class="grey darken-4 elevation-1 pt-6 px-7"
          style="max-width: 100%!important;"
        >
          <v-row>
            <v-col cols="12" md="4" sm="12" class="primary--text headline">
              <v-btn
                @click="reloadEncashments()"
                x-small fab color="primary" class="mr-3 elevation-0"
              >
                <v-icon class="black--text" small>fas fa-redo-alt</v-icon>
              </v-btn>
              <small>Encashment Management</small>
            </v-col>
            <v-col cols="12" md="4" sm="12" class="pa-2">
              <v-autocomplete
                v-model="userId"
                :items="users"
                :search-input.sync="searchUserValue"
                :loading="searchingUser"
                persistent-hint
                item-text="full_name"
                item-value="id" cache-items
                label="User" clearable
                prepend-inner-icon="fas fa-user"
                dense outlined dark
              >
              </v-autocomplete>
            </v-col>
            <v-col cols="12" md="4" sm="12" class="py-2">
              <v-select
                v-model="status"
                :disabled="loading"
                prepend-inner-icon="fas fa-money-check"
                :items="[
                  { text: 'Paid', value: 1 },
                  { text: 'Pending', value: 0  }
                ]"
                label="Status"
                clearable dense dark outlined
              ></v-select>
            </v-col>
          </v-row>
        </v-container>
      </template>
      <template v-slot:item.created_at="{ item }">
        {{ date(item.created_at, 'MMMM D, YYYY - hh:mm a') }}
      </template>
      <template v-slot:item.amount="{ item }">
        <span class="primary--text text-button" style="font-size: 18px!important;">₱</span>
        {{ money(item.amount) }}
      </template>
      <template v-slot:item.status="{ item }">
        <div class="justify-center layout">
          <v-switch
            @click="showConfirmDialog(item)"
            color="success darken-1"
            :input-value="item.confirmed"
            :success="item.confirmed"
            :disabled="item.confirmed"
            readonly
          ></v-switch>
          <v-chip
            small dark class="mt-5"
            :class="item.confirmed ? 'success' : 'primary black--text'"
          >
            {{ item.confirmed ? 'Paid' : 'Pending' }}
          </v-chip>
        </div>
      </template>
      <template v-slot:item.actions="{ item }">
        <div class="justify-center layout">
          <v-btn
            @click="selectedEncashment = item, showEncashmentDialog = true"
            dark fab x-small
            class="elevation-0 ml-2 blue"
          >
            <v-icon dark small>fas fa-search</v-icon>
          </v-btn>
          <v-btn
            @click="selectedEncashment = item, deleteEncashmentDialog = true"
            :disabled="item.confirmed"
            dark fab x-small
            class="elevation-0 ml-2 red"
          >
            <v-icon dark small>fas fa-trash</v-icon>
          </v-btn>
        </div>
      </template>
    </v-data-table>
    <!-- Show Encashment Dialog -->
    <v-dialog
      v-model="showEncashmentDialog" persistent max-width="500px"
      overlay-opacity="0.6"
      overlay-color="grey"
    >
      <v-card dark>
        <v-card-title class="pb-8">
          <v-icon class="mr-2" color="primary">fas fa-money-check</v-icon>
          <span class="headline primary--text"><small>Encashment Request Information</small></span>
          <v-spacer />
          <v-btn
            :disabled="submitting"
            @click="showEncashmentDialog = false"
            fab text small color="primary"
          >
            <v-icon>fas fa-times</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <v-card class="px-0 elevation-0">
            <v-card-text v-if="selectedEncashment.account && selectedEncashment.account">
              <table class="w-full">
                <tr>
                  <td class="py-1">Requested By</td>
                  <td class="py-1 white--text">{{ selectedEncashment.account.full_name }}</td>
                </tr>
                <tr>
                  <td class="py-1">Account</td>
                  <td class="py-1 white--text">{{ selectedEncashment.account.name }}</td>
                </tr>
                <tr>
                  <td class="py-1">Payment Channel</td>
                  <td class="py-1 white--text">{{ selectedEncashment.meta.payment_channel }}</td>
                </tr>
                <tr v-if="selectedEncashment.bank_account">
                  <td class="py-1">Bank Account</td>
                  <td class="py-1 primary--text">{{ selectedEncashment.bank_account }}</td>
                </tr>
                <tr>
                  <td class="py-1">Mobile Number</td>
                  <td class="py-1 white--text">{{ selectedEncashment.mobile_no }}</td>
                </tr>
                <tr>
                  <td class="py-1">Amount</td>
                  <td class="py-1 white--text">
                    <span class="primary--text text-button" style="font-size: 18px!important;">₱</span>
                    {{ selectedEncashment.amount }}
                  </td>
                </tr>
                <tr>
                  <td class="py-1">Tax</td>
                  <td class="py-1 white--text">
                    <span class="red--text text-button" style="font-size: 18px!important;">₱</span>
                    {{ selectedEncashment.meta.tax_fee }}
                  </td>
                </tr>
                <tr>
                  <td class="py-1">Admin Fee</td>
                  <td class="py-1 white--text">
                    <span class="red--text text-button" style="font-size: 18px!important;">₱</span>
                    {{ selectedEncashment.meta.admin_fee }}
                  </td>
                </tr>
                <tr>
                  <td class="py-1">Net Amount</td>
                  <td class="py-1 white--text">
                    <span class="primary--text text-button" style="font-size: 18px!important;">₱</span>
                    {{ selectedEncashment.meta.net_amount }}
                  </td>
                </tr>
                <tr>
                  <td class="py-1">Date Requested</td>
                  <td class="py-1 white--text">
                    {{ date(selectedEncashment.created_at, 'MMMM D, YYYY - hh:mm a') }}
                  </td>
                </tr>
              </table>
            </v-card-text>
          </v-card>
        </v-card-text>
        <v-card-actions class="pt-0">
            <v-spacer></v-spacer>
            <v-btn
              @click="showEncashmentDialog = false"
              color="primary" text
              :disabled="submitting"
            >
              <b>Close</b>
            </v-btn>
          </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- Toogle User Is Block Dialog -->
    <v-dialog
      v-model="approveEncashmentDialog" max-width="290"
      overlay-opacity="0.6"
      overlay-color="grey"
    >
      <v-card dark>
        <v-card-title class="headline">Are you sure ?</v-card-title>
        <v-card-text>
          This encashment will be paid.
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary" text
            :disabled="submitting"
            @click="approveEncashmentDialog = false"
          >
            <b>Cancel</b>
          </v-btn>
          <v-btn
            color="primary" text
            :loading="submitting"
            :disabled="submitting"
            @click="approveEncashment"
          >
            <b>Confirm</b>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- Delete Announcement Dialog -->
    <v-dialog
      v-model="deleteEncashmentDialog" max-width="290"
      overlay-opacity="0.6"
      overlay-color="grey"
    >
      <v-card dark>
        <v-card-title class="headline">Are you sure ?</v-card-title>
        <v-card-text>
          This encashment will be deleted.
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary" text
            :disabled="submitting"
            @click="deleteEncashmentDialog = false"
          >
            <b>Cancel</b>
          </v-btn>
          <v-btn
            color="primary" text
            :loading="submitting"
            :disabled="submitting"
            @click="deleteEncashment"
          >
            <b>Confirm</b>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import _ from 'lodash'

export default {
  data: () => ({
    showEncashmentDialog: false,
    approveEncashmentDialog: false,
    deleteEncashmentDialog: false,
    userId: null,
    searchingUser: false,
    searchUserValue: null,
    loading: true,
    submitting: false,
    pagination: { page: 1, itemsPerPage: 10 },
    selectedEncashment: {},
    errors: {},
    status: 0,
    users: [],
    search: null,
    headers: [
      { text: 'Requested By', align: 'start', sortable: true, value: 'account.full_name' },
      { text: 'Account', align: 'start', sortable: false, value: 'account.name' },
      { text: 'Date Requested', align: 'start', value: 'created_at' },
      { text: 'Amount', align: 'start', value: 'amount' },
      { text: 'Status', align: 'center', sortable: false, value: 'status', width: '120px' },
      { text: 'Actions', align: 'center', sortable: false, value: 'actions', width: '120px' }
    ],
    encashments: {},
    disablePagination: false
  }),
  async mounted () {
    await this.searchUser()
    this.getEncashments()
  },
  computed: {
    ...mapGetters({
      itemsPerPageOptions: 'itemsPerPageOptions'
    })
  },
  watch: {
    status () {
      this.reloadEncashments()
    },
    userId () {
      this.reloadEncashments()
    },
    searchUserValue: _.debounce(function () {
      this.searchUser()
    }, 500),
    pagination: {
      handler () {
        if (! this.disablePagination) {
          this.getEncashments()
        }
      },
      deep: true
    }
  },
  methods: {
    async searchUser () {
      try {
        this.searchingUser = true
        const users = await this.$axios.get('/admin/users', {
          params: {
            search: this.searchUserValue,
            page: 1, limit: 10, role_slug: 'user'
          }
        })
        this.users = users.data.data
      } catch (e) {
        this.showSnackbar('error', e.response ? e.response.data.message : e.message)
      } finally {
        this.searchingUser = false
      }
    },
    async reloadEncashments () {
      this.disablePagination = true
      this.pagination.page = 1
      await this.getEncashments()
      this.disablePagination = false
    },
    async getEncashments () {
      try {
        this.loading = true
        const p = this.pagination
        const payload = {
          page: p.page, limit: p.itemsPerPage,
          meta_type: 'Encashment',
          confirmed: this.status, user_id: this.userId
        }
        if (p.sortBy && p.sortBy.length > 0) {
          this.$set(payload, 'order_by', p.sortBy[0])
          this.$set(payload, 'order_desc', p.sortDesc[0] ? 1 : 0)
        }
        const encashments = await this.$axios.get('/admin/transactions', { params: payload })
        this.encashments = encashments.data
      } catch (e) {
        this.showSnackbar('error', e.response ? e.response.data.message : e.message)
      } finally {
        this.loading = false
      }
    },
    showConfirmDialog (item) {
        if (! item.confirmed) {
            this.selectedEncashment = item
            this.approveEncashmentDialog = true
        }
    },
    async approveEncashment () {
      try {
        this.submitting = true
        await this.$axios.put('/admin/transactions/approve-encashment/' + this.selectedEncashment.id)
        this.getEncashments()
        this.approveEncashmentDialog = false
        this.showSnackbar('success', 'Encashment has been paid successfully.')
      } catch (e) {
        this.showSnackbar('error', e.response ? e.response.data.message : e.message)
      } finally {
        this.submitting = false
      }
    },
    async deleteEncashment () {
      try {
        this.submitting = true
        await this.$axios.delete('/admin/transactions/' + this.selectedEncashment.id)
        this.getEncashments()
        this.deleteEncashmentDialog = false
        this.showSnackbar('success', 'Encashment has been deleted successfully.')
      } catch (e) {
        this.showSnackbar('error', e.response ? e.response.data.message : e.message)
      } finally {
        this.submitting = false
      }
    },
  }
}
</script>
